body {
  background-color: #232323;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

    .container {
    padding-top: 10px;
    background-color: white;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 10px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
    font-family: 'Times New Roman', Times, serif;
    font-size: 1.3rem;
    background-color: #232323;
  }
  
  h3 {
    color: red;
  }
  
  .scoreDiv {
    width: 100%;
    text-align: center;
    color: red;
  }
.card {
    color: black;
    border-radius: 2px;
    height: 150px;
    margin: 1rem;
    position: relative;
    width: 150px;
    text-align: left;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    border: solid 3px white;
      border-radius: 10px;
    vertical-align: center;
    padding-top: 25px;;
  }
  .card > .img-container > img {
    width: 70%
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }
  
  .card > .content > ul {
    list-style-type: none;
    padding: 10px;
  }
  
  .card > .content > ul > li {
    margin-top: 18px;
  }
.title {
    width: 100%;
    text-align: center;
    font-size: 3rem;
    color: red;
    margin: 0;
}
